import React, {Component} from  'react'
import $ from 'jquery'
import url from './audio/gbqq.mp3'


class Main extends Component{
    state={
        date:{},
    }
    componentDidMount(){
        this.print();
        setInterval(()=>{
                this.time(2019,4,11)
            },1000
        )
        var audio = document.getElementById("audio");
        setTimeout(()=>audio.play(),8500)
    }
    print = ()=>{
        $.fn.autotype = function() {
            var _this=$(this);
            var str=_this.html();
            // 正则替换代码行之间添加的多个空格，不去除换行输出会有明显的停顿：实际是在输出多个空格
            str=str.replace(/(\s){2,}/g,"$1");
            var index = 0;
            $(this).html('');
            var timer = function fn() {
                var args=arguments;
                var current = str.slice(index, index+1);
                // html标签完整输出,如：<p>
                if (current == '<'){
                    index = str.indexOf('>', index) + 1;
                }
                else{
                    index++;
                }
                //位运算符: 根据setInterval运行奇偶次来判断是否加入下划线字符“_”，使输入效果更逼真
                if (index < str.length-1){ //打印字符倒数第2个字符开始，不加下划线字符，以防止结束符可能会多输出一下划线字符
                    _this.html(str.substring(0, index) + (index & 1 ? '_' : ''));
                }else{
                    _this.html(str.substring(0, index));
                    clearTimeout(timer);
                };
                setTimeout(fn,200)
            };
            // 延迟1s开始
            setTimeout(timer,1000);
        };
        $("#autotype").autotype();
    }
    time =(year,month,day)=>{
        var dateNow = new Date();
        var dateJNR = new Date(year,month-1,day);
        // var anniversary = parseInt((dateNow - dateJNR) / (365*24*3600*1000))
        var d = parseInt((dateNow - dateJNR)/(24*3600*1000));
        var hour = parseInt(((dateNow - dateJNR)/(3600*1000))%24);
        var minute = parseInt((dateNow - dateJNR)/(1000*60)%60);
        var second = parseInt((dateNow - dateJNR)/1000%60);
        this.setState({date:{d:d,hour:hour,minute:minute,second:second}});
    };
    render(){
        const date =()=>{
            if (this.state.date.d!==undefined){
                const {d,hour,minute,second} = this.state.date
                return (<p>相识: <span className="date-text">{d}</span> 天 <span className="date-text">{hour}</span> 小时 <span className="date-text">{minute}</span> 分 <span className="date-text">{second}</span> 秒 </p>
                )
            }
        }
        return(
            <div className="App animated bounceInLeft">
            <div className="date">{date()}</div>
            <div id="autotype">
                <h1 style={{fontWeight:900}}>哈喽~yl</h1>
                <p >在开始之前，先放首歌当背景音乐吧！Music!</p>
                <p>在这无聊的晚上，合上复习书的瞬间，我又想到了你，后天就是520了，要不要送礼物，会不会打扰到你，也没有你的地址，我也不知道，我也不敢问，
                    想了很久，我还是决定写点东西给你。</p>
                <p>不知不觉认识已经有一个多月了，虽然见面次数不多，但那几天也是我到杭州后最开心的几天，也许你看不出来，
                    我也很羡慕那种开心难过都写脸上的人，有人为她开心，有人替他难过。人和人的表现总是不同的，
                    但遇到喜欢的人后心情却都是相同的，收到她的每个信息都很开心，看到她笑会更开心，给她分享自己的过往曾经很开心，听她分享她的每个喜怒哀乐很开心...
                </p>
                <p>想起和你在西湖边还有良渚漫步的时光，感觉总是很幸福，就好像小时候被爸爸妈妈走亲戚被夹在摩托车中间的感觉，酥酥的，很舒服，
                    所以那天问你我去滨江那边你会介意吗，其实也只是想多和你在一起而已。
                </p>
                <p>在我看来，春天里一颗小草的生长，它没有什么目的。风起时一匹公马发情，它也没有什么目的…
                    我要抱着草长马发情的伟大真诚去做一切事，而不是在人前羞羞答答的表演。我没有王小波的天赋，讲不出沁人心脾又轰轰烈烈的情话。我就想大声告诉你：我喜欢你！
                </p>
                <p>之前也和你说过我的人生轨迹，总是在离开，总是在相遇，每一次的离开刚开始总是轻松的，没过多久又会陷入自闭，
                    但我也很感谢相遇，在新疆认识了一群古灵精怪的小朋友，在苏州见识了很多事，认识了很多人，知道优秀是什么，在大连谈了一段纯净青涩的感情，随着她交换到深圳后戛然而止，
                    在北京有很多有理想的好朋友，常常促膝长谈，有同学创业要拉我技术入股，离开北京时一群朋友轮番劝我留下来，那时我已经是产学研团队的leader，也有公司给出了还不错的offer，
                    但是冥冥之中我还是来了杭州，我也相信我会在这有更好的收获。
                </p>
                <p>程序员还是木讷的，很多时候不愿想那么多事，不太懂浪漫，如果有什么说的做的不好的，麻烦你直接告诉我，我很皮实。
                    抱歉这两周霉运加身，不想传染给你，希望你天天开心~
                </p>
                <p>
                就这样，祝你520快乐！
                </p>
                <div style={{textAlign:'right'}}>
                    <p>喜欢你♥大双</p>
                    <p>2019年5月18日晚</p>
                </div>
            </div>
                <audio id="audio" src={url}></audio>
            </div>

        )
    }
}
export default Main



